* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    outline: none;
}

body {
    background: #e1e6eb;
}

button {
    cursor: pointer;
}

a {
    cursor: pointer;
}

.loader:not(.data-container .loader) {
    position: absolute;
    top: 50%;
    left: 50%;
    width: min(50vw, 200px);
    height: min(50vw, 200px);
    transform: translate(-50%, -50%);
}

.loader div,
.loader::before,
.loader::after {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background: #e74d4a;
    animation: pulse 1.5s infinite 0.3s;
    opacity: 0;
}

.loader:before {
    content: '';
    position: absolute;
    display: inline-block;
    background: url('/public/images/pokeball.svg');
    animation: pulse 1.5s infinite;
}

.loader::after {
    content: '';
    position: absolute;
    display: inline-block;
    animation: pulse 1.5s infinite 0.6s;
}

@keyframes pulse {
    0% {
        transform: scale(0.1);
        opacity: 0;
    }

    50% {
        opacity: 0.4;
    }

    100% {
        transform: scale(1.6);
        opacity: 0;
    }
}

