}

.types .fighting {
    background: #d04164;
}

.types .fire {
    background: #fd7d24;
}

.types .flying {
    background: #748fc9;
}

.types .ghost {
    background: #556aae;
}

.types .grass {
    background: #62b957;
}

.types .ground {
    background: #dd7748;
}

.types .ice {
    background: #61cec0;
}

.types .normal {
    background: #9da0aa;
}

.types .poison {
